import { useState } from "react";

const List = () => {
    const [isOpen] = useState(false);
    return (
        <>
        {isOpen && <section className='bg-light test-primary' id='modal'>
            <div className='container'>
                <h2>List</h2>
                <ul>
                    <li>1</li>
                    <li>2</li>
                    <li>2</li>
                    <li>2</li>
                    <li>5</li>
                </ul>
            </div>
        </section>}
        </>
    );
}

export default List;