import React, { useRef } from 'react';
import emailjs from '@emailjs/browser';

const Contact = () => {
    const form = useRef();

    const sendEmail = (e) => {
    e.preventDefault();

    emailjs
      .sendForm('service_wp6p117', 'template_yecbclo', form.current, {
        publicKey: '02_13jWJYcW6rgSkP',
      })
      .then(
        () => {
          console.log('SUCCESS!');
        },
        (error) => {
          console.log('FAILED...', error.text);
        },
      );
  };
    return ( 
        <>
            <section className="reveal active fade-in">
                <div className="container">
                    <h3 className="section-title">Get in touch</h3>
                    <div className="row row-content">
                        <div className="col-12">
                            <h2>Send Your Feedback</h2>
                            <form ref={form} onSubmit={sendEmail}>
                                <div className="form-element">
                                    <label htmlFor="fullname">Full name</label>
                                    <input type="text" name="fullname" id="fullname" placeholder="Full name" required />
                                </div>
                                <div className="form-element">
                                    <label htmlFor="email">Email</label>
                                    <input type="email" name="email" id="email" placeholder="Email" required />
                                </div>
                                <div className="form-element">
                                    <label htmlFor="message">Message</label>
                                    <textarea name="message" id="message" cols="30" rows="10" placeholder="Your message" required></textarea>
                                </div>
                                <div className="btn-group-rt">
                                    <button type="submit" className="btn-primary"><i className="fa fa-angles-right"></i> Send</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
            <section className="reveal fade-in">
                <div className="container">
                    <h3 className="section-title">Our Map</h3>
                    <div className="row-content">
                    <div><iframe title='location' width="100%" height="500" frameborder="0" scrolling="no" marginheight="0" marginwidth="0" src="https://maps.google.com/maps?width=100%25&amp;height=600&amp;hl=en&amp;q=1%20Grafton%20Street,%20Dublin,%20Ireland+(My%20Business%20Name)&amp;t=&amp;z=14&amp;ie=UTF8&amp;iwloc=B&amp;output=embed"><a href="https://www.gps.ie/">gps devices</a></iframe></div>
                    </div>
                </div>
            </section>
        </>
     );
}
 
export default Contact;