import { TypeAnimation } from "react-type-animation";

const Hero = () => {

    return (
        <>
            <section className="hero row align-center reveal active fade-in">
                <div className="container">
                    <div className="row align-center row-content">
                        <div className="col-md-8 col-lg-9">
                            <h1 className="text-primary">Hi welcome to <span className="text-detail">Marcharthon</span> Tours and Travels</h1>
                            <p>
                            <TypeAnimation
                                sequence={[
                                    // Same substring at the start will only be typed out once, initially
                                    'Air Ticketing',
                                    2000, // wait 1s before replacing "Mice" with "Hamsters"
                                    'Honeymoon Packages',
                                    2000,
                                    'Hoildays Packages',
                                    2000,
                                    'Safari Bookings',
                                    2000,
                                    'Tours and Travels',
                                    2000,
                                    'Hotel Bookings',
                                    2000
                                ]}
                                wrapper="span"
                                speed={50}
                                style={{ fontSize: '2em', display: 'inline-block' }}
                                repeat={Infinity}
                            />
                            </p>
                            <ul className="social-media">
                                <li><a href="/h" ><i className="fab fa-facebook"></i></a></li>
                                <li><a href="/h" ><i className="fab fa-twitter"></i></a></li>
                                <li><a href="/h" ><i className="fab fa-youtube"></i></a></li>
                                <li><a href="/h" ><i className="fab fa-instagram"></i></a></li>
                            </ul>
                        </div>
                        <div className="col-xs-6 col-md-4 col-lg-3 d-none-xs">
                           <img src="./assets/imgs/o.jpg" alt="hul"/>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}
export default Hero;