import React from 'react';

const Applications = () => {
    return (
        <>
            <section className='reveal active fade-in'>
                <div className='container'>
                    <div className='row-content row justify-center row'>
                        <div className='col-6 col-md-8 col-xs-10'>
                            <form>
                                <div className="form-element">
                                    <label htmlFor="fullname">Full name</label>
                                    <input type="text" name="fullname" id="fullname" placeholder="Full name" required />
                                </div>
                                <div className="form-element">
                                    <label htmlFor="email">Email</label>
                                    <input type="email" name="email" id="email" placeholder="Email" required />
                                </div>
                                <div className="form-element">
                                    <label htmlFor="email">Application Type</label>
                                    <input type="text" name="email" id="email" placeholder="Air Ticketing, Traveling, Visa Processings" required />
                                </div>
                                <div className="form-element">
                                    <label htmlFor="email">Phone</label>
                                    <input type="number" name="email" id="number" placeholder="+2567000000" required />
                                </div>
                                <div className="form-element">
                                    <label htmlFor="email">Appoinment day</label>
                                    <input type="date" name="date" id="email" required />
                                </div>
                                <div className="btn-group-rt">
                                    <button type="submit" className="btn-primary"><i className="fa fa-angles-right"></i> Send</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </section>
        </>
    );
}

export default Applications;
